}

.tinv-create-list {
  li {
	input[type=radio] {
	  margin-right: 10px;
	}

	+ li {
	  margin-top: 15px;
	}
  }

  .tinvwl-input-group + ul {
	margin-top: 25px;
  }
}

.tinv-search-list {
  margin-bottom: 36px;
}

.tinv-wishlist {
  .tinv-header {
	margin-bottom: 30px;
  }

  table {
	position: relative;
	table-layout: auto;
	margin-bottom: 30px;
	z-index: 2;
  }
}

.tinv-overlay {
  top: 0;
  left: 0;
  position: fixed;
  transition: opacity .3s ease, visibility .3s ease;
}

.tinv-wishlist {
