	  padding: 1em;
	  vertical-align: middle;
	}
  }

  .tinvwl-table-manage-list {
	.product-cb {
	  width: 35px;
	  text-align: center;
	}

	.product-remove {
	  width: 46px;
	  text-align: center;
	}
  }

  .product-remove button {
	display: flex;
	justify-content: center;
	align-content: center;
	margin: 0 auto;
	width: 27px;
	height: 27px;
	border-radius: 50%;
	padding: 0;
	box-shadow: none;
	border: none;
	background-color: #f7f7f7;
	color: #000000;
	font-size: 20px;
	padding-left: 3px;
  }

  .product-thumbnail {
	min-width: 100px;
	width: 100px;

