	  width: 80px;
	  text-align: center;
	}

	table .quantity .qty {
	  max-width: 100%;
	  width: 62px;
	  text-align: left;
	}
  }

  .product-action {
	width: 135px;
	text-align: center;

	.button {
	  width: 100%;
	}

