  tfoot {
	.tinvwl-to-right .tinv-create-list {
	  display: inline-block;
	  vertical-align: middle;

	  > a.button {
		margin-right: 0;
	  }
	}

	.tinvwl-to-left {
	  + .tinvwl-to-right {
		margin-top: -10px;
	  }

	  &:not(:empty) {
		float: left;
		width: 35%;
		margin-right: 2%;
	  }
	}

	.tinvwl-to-right {
	  float: left;
	  width: 63%;
	  text-align: right;

	  > * {
		margin: 10px 0 0;
		vertical-align: middle;

		+ * {
		  margin-left: 10px;
		}
	  }
	}

	.tinvwl-to-left:empty + .tinvwl-to-right {
	  width: 100%;
	}
  }

  .social-buttons + .tinv-wishlist-clear + .navigation-button {
	margin-top: 16px;
  }

  .navigation-button {
	margin-top: -10px;

	> li {
	  float: left;

	  > {
		.tinv-create-list > a, a {
		  margin-right: 30px;
		  margin-top: 10px;
		  display: inline-block;
		}

		.tinv-create-list > a.button, a.button {
		  margin-right: 10px;
		}
	  }

	  &:last-child > {
		.tinv-create-list > a, a {
		  margin-right: 0;
		}
	  }
	}
  }

  .social-buttons {
	text-align: right;

	> {
	  span, ul {
		display: inline-block;
		vertical-align: middle;
	  }

	  span {
		margin-right: 27px;
	  }
	}

	li {
	  float: left;
	  margin-right: 5px;
	  list-style: none;

	  &:last-child {
		margin-right: 0;
	  }

	  a.social {
		display: flex;
		align-content: center;
		justify-content: center;
		width: 2em;
		height: 2em;
		border-radius: 50%;
		text-align: center;
		box-shadow: none;
		border: 0;
		font-size: 20px;
		text-decoration: none;
		text-transform: none !important;

		&.white {
		  color: #ffffff;
		}

		&.dark {
		  color: #000000;
		}

		i {
		  line-height: 2em;
		}
	  }
	}
  }

  .navigation-button {
	.tinv-create-list > a.tinvwl-no-icon > i, li > a.tinvwl-no-icon > i {
	  display: none;
	}
  }

  .tinv-lists-nav {
	margin-top: 35px;
	margin-bottom: 35px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	width: 100%;

