	  flex-direction: column;
	  flex-basis: 100%;
	  flex: 1;
	  text-align: center;
	  justify-content: center;
	}

	> span {
	  font-weight: 700;
	}
  }

  .tinv-next {
	display: inline-block;
	float: left;
  }

  .tinv-prev {
	display: inline-block;
	float: left;
	margin-right: 35px !important;
  }

  .tinv-prev i {
	margin-left: 0;
	margin-right: 18px;
  }

  .tinv-next i {
	margin-left: 18px;
	margin-right: 0;
  }
}

