@font-face {
	font-family: 'tinvwl-webfont';
	src: url('../fonts/tinvwl-webfont.eot?xu2uyi');
	src: url('../fonts/tinvwl-webfont.eot?xu2uyi#iefix') format('embedded-opentype'),
	url('../fonts/tinvwl-webfont.ttf?xu2uyi') format('truetype'),
	url('../fonts/tinvwl-webfont.woff?xu2uyi') format('woff'),
	url('../fonts/tinvwl-webfont.svg?xu2uyi#tinvwl-webfont') format('svg');
	font-weight: normal;
	font-style: normal;
}

.ftinvwl {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'tinvwl-webfont' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

